import editorController from './controllers/editor-controller';
import {ngShowdown, markdownToHtml, stripHtmlFilter} from './components/showdown';

/**
 * ng-showdown
 */
angular.module('ng-showdown', ['ngSanitize']).
	provider('showdown', ngShowdown).
	directive('markdownToHtml', markdownToHtml).
	filter('stripHtml', stripHtmlFilter);

var showdownEditor = angular.module('showdown.editor', ['ng-showdown', 'ngAnimate', 'ngRoute']);

showdownEditor.config([function () {

}]);

showdownEditor.controller('editorCtrl', editorController);
