/** global showdown */
var ngShowdown = function() {
	var config = {
		extensions: [],
        sanitize: false
	};

	this.setOption = function(key, value) {
		config[key] = value;
		return this;
	};

	this.getOption = function(key) {
		if (config.hasOwnProperty(key)) {
			return config[key];
		} else {
			return undefined;
		}
	};

	this.loadExtension = function(name) {
		config.extensions.push(name);
		return this;
	};

	function showDownObj() {
		var converter = new showdown.Converter(config);

		this.makeHtml = function(md) {
			return converter.makeHtml(md);
		};

		this.stripHtml = function(text) {
			return String(text).replace(/<[^>]+>/gm, '');
		};

		this.getOption = function(key) {
			return converter.getOption(key);
		};

		this.setOption = function(key, value) {
			converter.setOption(key, value);
			return this;
		};
	}

	this.$get = function() {
		return new showDownObj();
	};
};

var markdownToHtmlDirective = function(showdown, sanitize, sce) {
	'ngInject';
	return {
    	restrict: 'A',
    	link: getLinkFn(showdown, sanitize, sce),
   		scope: {
      		model: '=markdownToHtml'
    	},
    	template: '<div ng-bind-html="trustedHtml"></div>'
  	};
};

var stripHtmlFilter = function(showdown) {
	'ngInject';
	return function (text) {
        return showdown.stripHtml(text);
    };
};

function getLinkFn($showdown, $sanitize, $sce) {
	return function (scope, element, attrs) {
	  scope.$watch('model', function (newValue) {
	    var showdownHTML;
	    if (typeof newValue === 'string') {
	      showdownHTML = $showdown.makeHtml(newValue);
	      scope.trustedHtml = ($showdown.getOption('sanitize')) ? $sanitize(showdownHTML) : $sce.trustAsHtml(showdownHTML);
	    } else {
	      scope.trustedHtml = typeof newValue;
	    }
	  });
	};
}

export default ngShowdown;
